import glamorous from 'glamorous'

const Score = glamorous.div(
  {
    margin: '0 10px',
    padding: 5,
    fontSize: 40,
    fontWeight: 'bold',
    color: '#bbb',

    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  }
)

export default Score




