import glamorous from 'glamorous'

const ScoreBoard = glamorous.div(
  {
    width: '100%',
    margin: '20px 0 0 0',
    padding: '10px 20px 0',
    borderTop: '2px dashed #bbb',

    display: 'flex',
    justifyContent: 'space-between',
  },
)

export default ScoreBoard






